<template>
  <section>
    <div :id="data.id" :style="{width: '100%', height: '400px'}"></div>
  </section>
</template>

<script>
// 
let echarts = require("echarts/lib/echarts");
// 
require("echarts/lib/chart/bar");
// title
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
export default {
  name: "hello",
  props: ["data"],
  data() {
    return {};
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // domecharts
      let myChart = echarts.init(document.getElementById(this.data.id));
      const yData = this.data.children.map(item => item.text);
      const xData = this.data.children.map(item => item.value);

      console.log(yData);

      // 
      myChart.setOption({
        title: { text: this.data.title },
        tooltip: {},
        xAxis: {
          data: yData,
          formatter: function(value) {
            return "{test|" + value + "}";
          },
          axisLabel: {
            rotate: 45, //45
            fontSize: 12,
            margin: 10,
            textStyle: {
              // color: "red"
            },
            rich: {
              test: {
                color: "red",
                lineHeight: 10
              }
            }
          }
        },
        yAxis: {
          axisLabel: {
            formatter(value) {
              return `${value}/10`;
            }
          }
        },
        grid: {
          // 
          x: 40,
          x2: 90,
          y2: 90 // y2 XZoom labelzoom
        },
        series: [
          {
            name: "",
            type: "bar",
            barWidth: 36, // K
            itemStyle: {
              normal: {
                color: "#1890ff"
              }
            },
            label: {
              normal: {
                show: true,
                formatter(params) {
                  // console.log(params);
                  return `${params.data}/10`;
                },
                position: "inside"
              }
            },
            data: xData
          }
        ]
      });
    }
  }
};
</script>

